//组件
import ContentBottom from "@/components/ContentBottom";
import ContentFile from "@/components/ContentFile";
import MarkdownRenderer from '~/components/MarkdownRenderer.vue';



import { getToken, setToken, removeToken } from "@/utils/auth";

export default {
  name: "Content",
  props: ["contentObj", "loveContentIds"],
  //import引入的组件需要注入到对象中才能使用
  components: { ContentBottom, ContentFile, MarkdownRenderer },
  data() {
    //这里存放数据
    return {
      //是否点赞
      zanBoolean: false,
      contentWidth: "width:605px;",
      contentId: null,
      isContentExpanded: false, // 初始状态下文本未展开
    };
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    let width = document.documentElement.clientWidth;
    if (width < 642) {
      this.contentWidth = "";
    }
    this.isZan(this.contentObj.contentId);
    // this.contentId = this.contentObj.contentId;
  },
  watch: {
    //监听值变化：
    // contentId: function () {
    //   this.isZan(this.contentId);
    // },
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  //方法集合
  methods: {
    //判断是否被点赞
    isZan(contentId) {
      if (this.loveContentIds.indexOf(contentId) == -1) {
        this.zanBoolean = false;
      } else {
        this.zanBoolean = true;
      }
    },
  },
};
