import touristApi from "@/api/tourist";

export default {
  //import引入的组件需要注入到对象中才能使用

  components: {},
  data() {
    //这里存放数据
    return {
      simpleHotList: [],
    };
  },

  //生命周期 - 创建完成（可以访问当前this实例）
  created() {
    this.getSimpleHotContent();
  },
  //方法集合
  methods: {
    getSimpleHotContent() {
      touristApi
        .getSimpleHotContent()
        .then((response) => {
          this.simpleHotList = response.data;
        })
        .catch();
    },
  },
};
